.tree .tree-node {
  padding-left: 1em;
}
.tree .tree-node .content {
  line-height: 24px;
  display: flex;
  align-items: stretch;
  padding-bottom: 2px;
}
.tree .tree-node .content .switcher {
  cursor: pointer;
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
}
.tree .tree-node .content .switcher .switcher-icon {
  transition-property: transform;
  transition-duration: 0.3s;
  transform: rotate(-90deg);
}
.tree .tree-node .content .switcher .switcher-icon.expanded {
  transform: rotate(0deg);
}
.tree .tree-node .content .switcher-placeholder {
  width: 24px;
}
.tree .tree-node .content .title-wrapper {
  padding: 0 4px;
  border-radius: 2px;
  cursor: pointer;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.tree .tree-node .content .title-wrapper:hover {
  background-color: #f5f5f5;
}
.tree .tree-node .content .title-wrapper.selected {
  background-color: #bae7ff;
}
.tree .tree-node .children-wrapper {
  transition: max-height, 0.3s;
  max-height: 0;
  overflow: auto;
}
.tree .tree-node .children-wrapper.expanded {
  display: block;
  max-height: 1000px;
}
